import { createMuiTheme, ThemeProvider, makeStyles } from '@material-ui/core/styles';
import {Typography} from '@material-ui/core';
import Data from './data.json';
import NavBar from './components/NavBar'
import Grid from './components/Grid'
import Footer from './components/Footer'
import './App.css';
import SecurityIcon from '@material-ui/icons/Security';

  const theme = createMuiTheme({
    palette: {
      primary: {
        main: "#006699",
      },
      secondary: {
        main: "#c7d8ed",
      },
    },
    typography: {
      fontFamily: [
        'Montserrat'
      ],
      h4: {
        fontWeight: 600,
        fontSize: 28,
        lineHeight: '2rem',
        },
      h5: {
        fontWeight: 100,
        lineHeight: '2rem',
      },
    },
  });

  const styles = makeStyles({
    wrapper: {
      width: "65%",
      margin: "auto",
      textAlign: "center"
    },
    bigSpace: {
      marginTop: "5rem"
    },
    littleSpace:{
      marginTop: "2.5rem",
    },
    grid:{
      display: "flex", 
      justifyContent: "center",
      alignItems: "center",
      flexWrap: "wrap", 
    },
  })




  function App() {
    const classes = styles(); 


  return (
    <div className="App">
      <ThemeProvider theme={theme}>
          <NavBar/>
          <div className={classes.wrapper}>
            <Typography variant="h4" className={classes.bigSpace} color="primary">
                Better Medicare Benefits Are Like Having More Money In Your Pocket Every Month
            </Typography>
            <Typography variant="h5" className={classes.littleSpace} color="primary">
                You want Medicare benefits that make sense for you and your unique needs. But you also need benefits that you can afford. You might be surprised to learn that there are better Medicare benefits and plans you can afford.
            </Typography>
        </div>
          <div className={`${classes.grid} ${classes.bigSpace} ${classes.posts}`}>
              { Data.map(post => {
                return(
                  <div key={  post.id  }>
                    <Grid icon={<SecurityIcon style={{fill: "#4360A6", height:"125", width:"125"}}/>}  title={ post.title } btnTitle={ post.title } />
                    <h5>key values: {post.num1} and {post.num2} </h5>
                     <p>Sum: { post.num1 + post.num2 }</p>
                  </div>
                )
              }) }
          </div>

          <div className={classes.bigSpace}>
              <Footer/>
          </div>
      </ThemeProvider>
    </div>
  );
}



export default App;
