import React from 'react'
import {Button} from '@material-ui/core'
import {withStyles} from '@material-ui/core/styles' // a hook to write css inside component

const StyledButton = withStyles({ // function with object inside of it
    root: {
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        height: "44px",
        padding: "0 25px",
        boxSizing: "border-box",
        borderRadius: 0, 
        background: "#4f25f7",
        color: "#fff",
        transform: "none",
        boxShadow: "6px 6px 0 0 #c7d8ed",
        transition: "background .3s,border-color .3s,color .3s",
        "&:hover": {
            backgroundColor:  "#4f25f7"
          },
    },
    label: {
      textTransform: 'capitalize',
    },
  })(Button);  // 2nd call to Button function


function CustomBtn(props) {
    return (
        <StyledButton variant="container">{props.txt}</StyledButton>
    )
}



export default CustomBtn
